import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {RouterModule, Routes} from '@angular/router';


import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { LoginComponent } from './pages/login/login.component';
import { ProfileComponent } from './pages/profile/profile.component';
import { AddUserComponent } from './pages/add-user/add-user.component';
import { AddRouteComponent } from './pages/add-route/add-route.component';
import { AdminService } from './services/adminService/admin.service';
import { AuthGuardService } from './Guards/auth.guard';
import { RouteDetailsComponent } from './pages/route-details/route-details.component';


const appRoutes:Routes = [
  {path:'', component: LoginComponent},
  {path:'profile', component:ProfileComponent, canActivate: [AuthGuardService]},
  {path:'addUser', component:AddUserComponent, canActivate: [AuthGuardService]},
  {path:'addRoute', component:AddRouteComponent, canActivate: [AuthGuardService]},
  {path:'routeDetails', component:RouteDetailsComponent, canActivate: [AuthGuardService]},
]
@NgModule({
 

  declarations: [
    AppComponent, NavbarComponent, LoginComponent, ProfileComponent, AddUserComponent, AddRouteComponent, 
    RouteDetailsComponent
  ],
  imports: [
    
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes)

  ],
  providers: [AdminService, AuthGuardService],
  bootstrap: [AppComponent]
})
export class AppModule { }



